	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/"],"guides":["/guides/quick-start-guide/"],"configurations":["/configurations/overview/","/configurations/details/","/configurations/custom-webpack-config/"],"testing":["/testing/manual-testing/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom webpack configuration","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/configurations/overview/"},"frontmatter":{"title":"Configuration overview","id":"overview"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}},{"node":{"fields":{"slug":"/configurations/details/"},"frontmatter":{"title":"Detailed configuration overview","id":"details"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},{"node":{"fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}}]},"markdownRemark":{"html":"<p>Wingsuit is a designsystem for twig based UI Components. The components are developed independently in storybook and can be used by other “apps” like drupal. </p>\n<p>A wingsuit component is defined by an wingsuit.yml.\nThe wingsuit.yml is an extended variant of the <a href=\"https://ui-patterns.readthedocs.io/en/8.x-1.x/content/patterns-definition.html\">ui_patterns.yml</a>. </p>\n<p><b>Wingsuit uses this configuration file to:</b></p>\n<p><b>Generate Storybook stories</b><br>\nEach variant is a storybook story. Fields and Settings are editable with knobs.\n[SCREENSHOT]</p>\n<p><b>Automatic Storybook documentation</b><br>\nA nice documentation page is created directly from the wingsuit.yml.\n[SCREENSHOT]</p>\n<p><b>Drupal integration</b><br>\nWith the <a href=\"https://www.drupal.org/project/wingsuit_companion\">wingsuit_companion</a> patterns can be used with zero configuration in drupal.\n[SCREENSHOT]</p>\n<h3 id=\"bstay-in-syncb\"><a href=\"#bstay-in-syncb\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><b>Stay in sync</b></h3>\n<p>One big challenge for design system are to stay in sync between the apps.\nWingsuit solves the problem with a common component configuration. The <b>wingsuit.yml.</b></p>\n<h3 id=\"bfrontend-frameworksb\"><a href=\"#bfrontend-frameworksb\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><b>Frontend frameworks</b></h3>\n<p>Wingsuit ships with <a href=\"https://tailwindcss.com/\">tailwind</a> and <a href=\"https://github.com/alpinejs/alpine\">alpinejs</a>.\nThe frameworks can easily replaced with other frameworks like <a href=\"https://getbootstrap.com\">bootstrap 4</a> or <a href=\"https://get.foundation/sites/docs/\">foundation 6</a>.</p>","fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},"pathContext":{"slug":"/basics/introduction/"}}

